import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output, SimpleChanges } from '@angular/core';
import { BcFloatMenuBehavior } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';

export enum NumberOfPreloaders {
  ONE = 1,
  TWO = 2,
  THREE = 3,
  FOUR = 4,
}

@Component({
  selector: 'bc-float-menu',
  templateUrl: './bc-float-menu.component.html',
})
export class BcFloatMenuComponent implements AfterViewInit, OnInit {
  bcFloatMenuBehavior: BcFloatMenuBehavior;
  @Input() componentId: string = this.bcHelperService.getId(this);
  @Input() checkbox: boolean = false;
  @Input() radioButtons: boolean = false;
  @Input() options: Array<Options> = [];
  @Input() arrowCenter: boolean = false;
  @Input() arrowLeft: boolean = false;
  @Input() arrowRight: boolean = false;
  @Input() positionRight: boolean = false;
  @Input() positionLeft: boolean = false;
  @Input() selectedIcon: boolean = false;
  @Input() row: any = null;
  @Input() data: any;
  /**
   * Parametro que permite mostrar el preloader
   * @param havePreloader
   * @type boolean
   * @default false
   */
  @Input() havePreloader: boolean = false;
  /**
   * Parametro que permite configurar el numero de preloader
   * @param numOfpreloaders
   * @type number
   * @default 1
   * @description valid values: 1, 2, 3, 4
   */
  @Input() numOfpreloaders: NumberOfPreloaders;
  /**
   * Parametro que permite configurar el ancho del preloader
   * @param floatMenuWidth
   * @type string
   * @default 'default'
   * @description valid values: 'full', 'default'
   * @description 'full' = 100% width
   * @description 'default' = preloader width (100px)
   */
  @Input() floatMenuWidth: 'full' | 'default' = 'default';
  @Output() onChange: EventEmitter<any> = new EventEmitter();
  dropdownId = this.bcHelperService.getId(this);
  @Output() changed: EventEmitter<any> = new EventEmitter();

  preloaderList: Array<number> = []

  constructor(private bcHelperService: BcHelperService) { }
  ngOnInit(): void {
    this.generatePreloaderList();
  }

  ngAfterViewInit() {
    this.bcFloatMenuBehavior = new BcFloatMenuBehavior();
    this.bcFloatMenuBehavior.setId(this.componentId);
    const element = this.bcFloatMenuBehavior.getMySelf().querySelector(`[class~='bc-float-menu']`);
    this.bcFloatMenuBehavior.setElement(element);
    this.bcFloatMenuBehavior.setUp();
  }

  ngOnChanges(change: SimpleChanges) {
    const TIMEOUT_MS = 500;
    if (change.options.currentValue.length > 0 && this.havePreloader) {
      BcFloatMenuBehavior.closeAll();
      this.bcFloatMenuBehavior.setUp();
      setTimeout(() => {
        this.bcFloatMenuBehavior.setUp();
      }, TIMEOUT_MS);
    }
  }
  generatePreloaderList() {
    this.preloaderList = Array.from({ length: this.numOfpreloaders }, (_, index) => index + 1);
  }

  selectOption(event, optionId: string): void {
    this.onChange.emit({
      optionSeleted: optionId,
      rowData: this.row ? this.row : null,
      customData: this.data ? this.data : null,
    });
  }
}
interface Options {
  id?: string;
  iconLeft?: string;
  iconRight?: string;
  mainText?: string;
  secondaryText?: string;
  imagRight?: string;
  imagLeft?: string;
  altImg?: string;
}
